.main {
    padding: 40px 0px;
    /*text-align: left;*/
    /*height: 100vh;*/
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    width: 100%;
    /*border: 1px solid;*/
    display: flex;
    /*justify-content: space-around;*/
    align-items: center;
    flex-wrap: wrap;
}

.content{
    display: flex;
    flex-direction: column;
    /*align-items: center;*/
    margin: 0px 20px;
}

.text {
    display: flex;
    flex-direction: column;
    /*background-color: lavender;*/
    font-weight: bold;
}

.photo {
    height: 210px;
    width: 210px;
    background-size: cover;
    max-width: 100%;
    border-radius: 50%;
    /*background-image: url(avtar-01.jpg);*/
    background-image: url(photo.jpg);
    /*background-color: lavender;*/
    margin: 0px 20px;
}
h1{
    font-size: 38px;
    font-weight: 400;
    text-transform: capitalize;
    margin-bottom: 5px;
}
.tags{
    margin-bottom: 10px;
}
.tags ul{
    padding-inline-start: 0px;
}
.tags li{
    /*background-color: rgba(1, 1, 1, 0.63);*/
    /*color: #fff;*/
    margin: 0px 6px;
    display: inline-block;
    padding: 4px 10px;
    border: 1px solid #010101;
    border-radius: 4px;
    font-weight: 500;
}
.link{
    /*margin-top: 10px;*/
}
button{
    color: #010101;
    background-color: transparent;
    border: 1px solid #CBCFD0;
    color: #010101;
    font-family: 'Quicksand';
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.5px;
    border-radius: 50rem;
    padding: 10px 15px;
}
button:hover{
    color: #fff;
    background-color: #010101;
    border: 1px solid #010101;
}
